@import url(https://fonts.googleapis.com/css2?family=Noto+Sans:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Annie+Use+Your+Telescope&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@media all and (max-width: 1900px) {
  /* Projects */
  .Showcase__div {
    height: 650px !important;
  }
}
@media all and (max-width: 1700px) {
  /* Projects */
  .Showcase__div {
    height: 550px !important;
  }

  /* Skills */
  .skills_container {
    grid-template-columns: repeat(auto-fit, 200px) !important;
  }
}
@media all and (max-width: 1500px) {
  /* Projects */
  .Showcase__div {
    height: 450px !important;
  }
  .Projects__title p {
    margin-bottom: 2em !important;
  }
  .Projects__title2 p {
    margin-bottom: 2em !important;
  }
}
@media all and (max-width: 1300px) {
  /* App.css */
  .container {
    padding: 0 10em !important;
  }
  /* Projects */
  .Showcase__div {
    height: 350px !important;
  }
  /* Skills */
  .logoSize {
    font-size: 4em !important;
  }
}
@media all and (max-width: 1000px) {
  /* Projects */
  .Showcase__div {
    height: 250px !important;
  }
}

/* Medium Screen */
@media all and (max-width: 950px) {
  /* App.css */
  .container {
    padding: 0 7em !important;
  }

  /* About */
  .About__container {
    display: -webkit-flex !important;
    display: flex !important;
    -webkit-flex-direction: column !important;
            flex-direction: column !important;
    -webkit-align-items: center !important;
            align-items: center !important;
    /* margin-top: 5em !important; */
  }
  .About__container img {
    float: none !important;
    padding-right: 0 !important;
  }
  .About__text {
    margin-top: 4em !important;
    margin-left: 0 !important;
  }
  .button {
    display: grid;
    place-content: center;
  }

  /* Educations */
  .Educations h1 {
    font-size: 2em !important;
  }
  .educations {
    padding: 2em !important;
  }

  /* Projects */
  .Projects__title h1 {
    font-size: 2em !important;
  }
  .Showcase__div img {
    width: 70% !important;
  }
  .Showcase__div {
    height: 270px !important;
  }

  /* Contact */
  .Contact__title {
    font-size: 2em !important;
  }

  /* Nav bar */
  .Nav__items {
    -webkit-justify-content: center !important;
            justify-content: center !important;
  }
  .Nav__items a {
    font-size: 1em !important;
     margin-right: 1.5em !important;
  }
}

@media all and (max-width: 700px) {
  /* Projects */
  .Showcase__div {
    height: 240px !important;
  }
  .Showcase__div img {
    width: 100% !important;
  }

  /* Nav bar */
  .Nav__items {
    -webkit-justify-content: center !important;
            justify-content: center !important;
  }
  .Nav__items a {
    font-size: 0.9em !important;
     margin-right: 1.5em !important;
  }
}

/* Small Screen */
@media all and (max-width: 550px) {
  /* App.css */
  .container {
    padding: 0 4em !important;
  }
  /* Home */
  .Home {
    height: 350px !important;
  }
  .Home__name {
    font-size: 2em !important;
  }
  .Home__title {
    top: 20% !important;
  }

  /* About */
  .About__container {
    margin-top: 3em !important;
  }
  .About__container img {
    width: 10em !important;
  }
  .About__text p {
    font-size: 0.9em !important;
  }

  /* Educations */
  .Education {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column !important;
            flex-direction: column !important;
    -webkit-align-items: unset !important;
            align-items: unset !important;
  }
  .Education__icon {
    margin-bottom: 10px;
    font-size: 2em !important;
    -webkit-justify-content: flex-start !important;
            justify-content: flex-start !important;
    -webkit-align-items: flex-start !important;
            align-items: flex-start !important;
  }
  .years,
  .school {
    font-size: 0.6em !important;
  }
  .degree {
    font-size: 0.9em !important;
  }
  .educations {
    padding: 1em 2em !important;
    border-radius: 0 !important;
  }
  .Education__text {
    margin-left: 0em !important;
  }

  /* Nav bar */
  .Nav__items a {
    font-size: 0.7em !important;
     margin-right: 1.7em !important;
  }
}

/* XS Screen */
@media all and (max-width: 450px) {
  /* App.css */
  .container {
    padding: 0 2em !important;
  }
  /* Projects */
  .Carousel-buttonWrapper-6 button {
    width: 29px !important;
    height: 29px !important;
  }
  .MuiSvgIcon-root {
    width: 8px !important;
    height: 8px !important;
  }

  .websites {
    grid-template-columns: initial !important;
  }

  /* Skills */
  .skills_container {
    grid-template-columns: repeat(auto-fit, 100px) !important;
  }

  /* Nav bar */
  .Nav__items a {
    font-size: 0.6em !important;
    margin-right: 2em !important;
  }
}

@media all and (max-width: 360px) {
  /* App.css */
  .container {
    padding: 0 1em !important;
  }

  /* Projects */
  .Carousel-buttonWrapper-6 button {
    width: 15px !important;
    height: 15px !important;
  }

  /* Nav bar */
  .Nav__items a {
    font-size: 0.3em !important;
  }

  /* Skills */
  .skills_container {
    grid-template-columns: repeat(auto-fit, 90px) !important;
  }
  .logoSize {
    font-size: 3em !important;
  }
  .Skill p {
    font-size: 1em !important;
  }
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Noto Sans', sans-serif;
}

.center {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.container {
  width: 100%;
  padding: 0 15em;
}

.title {
   font-size: 3em;
  margin-bottom: 0.3em;
}

.subtitle{
  color: #666;
}


/* @import url('https://fonts.googleapis.com/css2?family=Lato&display=swap'); */



.Home {
  width: 100%;
  height: 100vh;
  background: linear-gradient(to right, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)),
    url("https://images.unsplash.com/photo-1499571883643-44e68963973d?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=2560&q=80")
      center;
  background-size: cover;
  color: white;
}

.Home__name {
  font-size: 4.6em;
  margin-bottom: 0.5em;
}

.Home__title {
  position: absolute;
  top: 35%;
  left: 22%;
}

.typedStrings {
/* font-family: 'Lato', sans-serif; */
font-family: 'Annie Use Your Telescope', cursive;
  text-transform: uppercase;
  font-size: 1.3em;
}

.About__container {
  width: 100%;
  margin: 8em 2em 0 2em;
  text-align: left;
  display: -webkit-inline-flex;
  display: inline-flex;
}

.About__container img {
  width: 20em;
  padding-right: 4em;
}

.About__info a {
  color: #666;
  text-decoration: none;
}

.About__info span {
 color:dodgerblue;
}

.About__text h3 {
  font-size: 1.7em;
}

.About__text p {
  color: #666;
  line-height: 1.7;
  margin: 1em 0;
  font-size: 1em;
}

.resume_button {
  background-color: #766161 !important;
  color: white !important;
  padding: 1em 2em !important;
}

.resume_button:hover {
  background-color: #ffc288 !important;
}

.Education {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  margin: 2.5em 0;
}

.Education__icon {
  color: #868686;
  -webkit-flex: 0.5 1;
          flex: 0.5 1;
}

.Education__text {
  margin-left: 1em;
  -webkit-flex: 2 1;
          flex: 2 1;
}

.years,
.school {
  font-size: 0.9em;
  color: #868686;
  line-height: 2;
}

.school {
  font-weight: bold;
}

.degree {
  font-size: 1.1em;
  font-weight: bold;
  color: #666;
  line-height: 2;
}

.Educations {
  margin-top: 8em;
  width: 100% ;
}

/* .Educations h1 {
  font-size: 3em;
  margin-bottom: 0.3em;
} */

/* .Educations p {
  color: #666;
} */

.educations {
  background-color: #eaeaea85;
  border-radius: 15px;
  margin-top: 3em;
  padding: 2em 10em;
  width: 100%;

}

.Showcase__div img {
  width: 60%;
}
.Showcase {
 
}
.Showcase__div {
  display: grid;
  place-items: center;
  height: 700px;
}

.Projects {
  margin-top: 8em;
  text-align: center;
}

.Projects__title h1 {
  margin-bottom: 1em;
}

.Projects__title h3, .Projects__title2 h3 {
  font-size: 2em;
}

.Projects__title p {
  margin-bottom: 1em;
}

.Projects__title2 p {
  margin-bottom: 4em;
}

.Projects__cakes {
  margin-bottom: 6em;
  width: 100%;
}

.Projects__websites {
  width: 100%;
}

.websites {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  grid-gap: 2em;
}

.websiteCard {
  height: 250px;
  border-radius: 5px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  color: white;
}

.websiteCard:hover > div {
  visibility: visible;
}

.cardContent {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 194, 136, 0);
  transition: background-color 0.3s;
  visibility: hidden;
  border-radius: 5px;
}

.cardContent:hover {
  background-color: rgba(46, 38, 38, 0.8);
}

.cardContent p {
    font-weight: bold;
    font-size: 1.5em;
    margin-bottom: 1.5em;
}

.cardLinks a{
  text-decoration: none;
  color: white;
  margin: 1em;
}

.cardLinks a:hover {
  color: #ffc288;
}

.Skills {
  margin-top: 8em;
  width: 100% ;
}

.Skills p {
  margin-bottom: 3em;
}

.logoSize {
  font-size: 6em;
}

.skills_container {
  display: grid;
  grid-template-columns: repeat(auto-fit, 300px);
  -webkit-justify-content: space-between;
          justify-content: space-between;

}


.Skill p {
  font-size: 1.2em;
  font-weight: bold;
}

.Contact {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  margin-top: 8em;
  text-align: center;

}

.Contact__title {
  font-size: 3em;
  margin-bottom: 0.3em;
  /* margin-top: 1em; */
}

.Contact_subtitle {
  margin-bottom: 5em;
}

.ContactInfo {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  text-align: left;
  text-align: initial;
  margin-bottom: 4em;
}

.contact_icon {
  -webkit-flex: 0.18 1;
          flex: 0.18 1;
  color: #766161;
}

.contact_info {
  margin-left: 3em;
  -webkit-flex: 1 1;
          flex: 1 1;
  line-height: 2;
}

.contact_info h4 {
  font-size: 1.3em;
}
.Nav {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 1;
  padding: 1.5em 5em;
  transition-timing-function: ease-in;
  transition: 0.5s;
}
.Nav__items {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  font-weight: bold;
}

.Nav__items a {
  margin-right: 3em;
  cursor: pointer;
  text-decoration: none;
}

.nav_home {
  background-color: transparent;
}

.nav_home a {
  color: white;
}

.nav_scroll {
  background-color: rgba(255, 255, 255);
  box-shadow: 0px 5px 10px -8px #000000;
}

.nav_scroll a {
  color: black;
}

.active {
  color: #ffc288 !important;
}

.Footer {
    padding: 5em;
    background-color: black;
    color: white;
    text-align: center;
}
